"use client";
import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
// Note: axios should be installed in your project
import { db, auth } from "@/firebaseConfig";
import { doc, setDoc, getDoc, updateDoc, arrayUnion } from "firebase/firestore";
import { onAuthStateChanged } from "firebase/auth";
import axios from "axios";


const allGenres = [
  "Action", "Adventure", "Animation", "Biography", "Comedy", "Crime",
  "Documentary", "Drama", "Family", "Fantasy", "Film-Noir", "History",
  "Horror", "Music", "Musical", "Mystery", "Romance", "Sci-Fi", "Sport",
  "Thriller", "War", "Western", "Reality-TV", "Short", "Talk-Show", "Game-Show",
  "Superhero", "Detective", "Classic", "Survival", "Cyberpunk", "Psychological",
];

const genreEmojis = {
  "Action": "💥", "Adventure": "🗺️", "Animation": "🎨", "Biography": "📖", "Comedy": "😂",
  "Crime": "🕵️", "Documentary": "🎬", "Drama": "🎭", "Family": "👨‍👩‍👧‍👦", "Fantasy": "🧙‍♂️",
  "Film-Noir": "🌙", "History": "⏳", "Horror": "👻", "Music": "🎵", "Musical": "🎼",
  "Mystery": "🔍", "Romance": "💕", "Sci-Fi": "🚀", "Sport": "⚽", "Thriller": "🔥",
  "War": "⚔️", "Western": "🤠", "Reality-TV": "📺", "Short": "🎞️", "Talk-Show": "🎤",
  "Game-Show": "🎮", "Superhero": "🦸‍♂️", "Detective": "🔎", "Classic": "🏛️",
  "Survival": "🏕️", "Cyberpunk": "🤖", "Psychological": "🧠"
};

interface Movie {
  Title: string;
  Year: string;
  imdbID: string;
  Poster: string;
  Genre?: string;
  imdbRating?: string;
  Plot?: string;
  Actors?: string;
}

export default function GenrePage() {
  const router = useRouter();
  const apikey = "https://www.omdbapi.com/?apikey=a4d5b9";

  const [selectedGenres, setSelectedGenres] = useState<string[]>([]);
  const [movies, setMovies] = useState<Movie[]>([]);
  const [loading, setLoading] = useState(false);
  const [addedMessage, setAddedMessage] = useState<string | null>(null);
  const [userId, setUserId] = useState<string | null>(null);
  const [modalMovie, setModalMovie] = useState<Movie | null>(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) setUserId(user.uid);
    });
    return () => unsubscribe();
  }, []);

  const toggleGenre = (genre: string) => {
    setSelectedGenres((prev) =>
      prev.includes(genre) ? prev.filter((g) => g !== genre) : [...prev, genre]
    );
  };

  const clearGenres = () => {
    setSelectedGenres([]);
    setMovies([]);
  };

  const addToList = async (movie: Movie) => {
    if (!userId) return;
    const userDoc = doc(db, "watchlists", userId);

    try {
      const snapshot = await getDoc(userDoc);
      if (!snapshot.exists()) {
        await setDoc(userDoc, { movies: [movie] });
      } else {
        await updateDoc(userDoc, {
          movies: arrayUnion(movie),
        });
      }
      setAddedMessage(`${movie.Title} has been added to your list.`);
      setTimeout(() => setAddedMessage(null), 2500);
    } catch (error) {
      console.error("Error saving to Firestore:", error);
    }
  };

  const fetchMoviesByGenre = async () => {
    if (selectedGenres.length === 0) return;
    setLoading(true);

    try {
      const queries = await Promise.all(
        selectedGenres.map((genre) => axios.get(`${apikey}&s=${genre}`))
      );

      const results = (
        await Promise.all(
          queries
            .map((res) => (res.data as { Search?: any[] }).Search || [])
            .flat()
            .map(async (movie: any) => {
              const { data } = await axios.get(`${apikey}&i=${movie.imdbID}`);
              return data;
            })
        )
      ).filter((m) =>
        selectedGenres.some((g) =>
          (m as Movie).Genre?.toLowerCase().includes(g.toLowerCase())
        )
      );

      setMovies(results as Movie[]);
    } catch (err) {
      console.error("Failed to fetch genre movies:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMoviesByGenre();
  }, [selectedGenres]);

  return (
    <div className="min-h-screen w-full relative overflow-hidden text-white bg-black">
      {/* Animated Background */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-br from-purple-900/20 via-black to-blue-900/20"></div>
        <div className="absolute inset-0 opacity-30">
          <div className="absolute top-20 left-20 w-96 h-96 bg-purple-500/10 rounded-full blur-3xl animate-pulse"></div>
          <div className="absolute bottom-20 right-20 w-80 h-80 bg-blue-500/10 rounded-full blur-3xl animate-pulse animation-delay-1000"></div>
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-72 h-72 bg-pink-500/10 rounded-full blur-3xl animate-pulse animation-delay-2000"></div>
        </div>
      </div>

      {/* Film Strip Decoration */}
      <div className="absolute left-0 top-0 w-12 h-full bg-gradient-to-b from-gray-800/20 to-gray-900/20 opacity-30">
        {[...Array(15)].map((_, i) => (
          <div key={i} className="h-16 border-b border-gray-600/30 flex items-center justify-center">
            <div className="w-4 h-4 rounded-full bg-gray-500/40"></div>
          </div>
        ))}
      </div>

      {/* Loading Screen */}
      {loading && (
        <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/90 backdrop-blur-xl">
          <div className="relative">
            <div className="w-20 h-20 border-4 border-white/20 rounded-full animate-spin border-t-white"></div>
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="w-8 h-8 bg-white rounded-full animate-pulse"></div>
            </div>
          </div>
          <p className="text-white/80 text-lg mt-6 animate-pulse">Discovering cinematic gems...</p>
        </div>
      )}

      {/* Toast Notification */}
      {addedMessage && (
        <div className="fixed top-6 left-1/2 transform -translate-x-1/2 z-50 bg-white/10 backdrop-blur-lg border border-white/20 text-white px-6 py-3 rounded-xl shadow-2xl animate-bounce">
          <div className="flex items-center gap-2">
            <span className="text-green-400">✓</span>
            {addedMessage}
          </div>
        </div>
      )}

      <div className="relative z-10 p-6 max-w-7xl mx-auto">
        {/* Header Section */}
        <div className="text-center mb-12">
          <div className="mb-6">
            <h1 className="text-5xl md:text-7xl font-bold mb-4 bg-gradient-to-r from-white via-purple-200 to-blue-200 bg-clip-text text-transparent">
              GENRE DISCOVERY
            </h1>
            <div className="flex items-center justify-center space-x-4 mb-4">
              <div className="h-0.5 w-20 bg-gradient-to-r from-transparent via-white to-transparent"></div>
              <span className="text-white/60 text-lg tracking-widest">🎬 CURATED COLLECTIONS 🎬</span>
              <div className="h-0.5 w-20 bg-gradient-to-r from-transparent via-white to-transparent"></div>
            </div>
            <p className="text-white/70 text-lg max-w-2xl mx-auto">
              Explore cinematic universes through carefully curated genre collections. 
              Select your favorite genres and discover your next obsession.
            </p>
          </div>
        </div>

        {/* Genre Selection */}
        <div className="mb-10">
          <div className="bg-white/5 backdrop-blur-lg border border-white/10 rounded-2xl p-6 mb-6">
            <h2 className="text-2xl font-semibold mb-4 text-center">Choose Your Adventure</h2>
            <div className="flex flex-wrap gap-3 justify-center max-w-6xl mx-auto">
              {allGenres.map((genre) => (
                <button
                  key={genre}
                  onClick={() => toggleGenre(genre)}
                  className={`group relative px-4 py-2 rounded-xl border transition-all duration-300 transform hover:scale-105 ${
                    selectedGenres.includes(genre)
                      ? "bg-gradient-to-r from-purple-500/30 to-blue-500/30 text-white border-purple-400/50 shadow-lg shadow-purple-500/20"
                      : "bg-white/5 text-white/80 border-white/20 hover:bg-white/10 hover:border-white/40"
                  }`}
                >
                  <span className="text-sm mr-2">{genreEmojis[genre as keyof typeof genreEmojis] || "🎭"}</span>
                  <span className="font-medium">{genre}</span>
                  {selectedGenres.includes(genre) && (
                    <div className="absolute -top-1 -right-1 w-3 h-3 bg-purple-400 rounded-full animate-ping"></div>
                  )}
                </button>
              ))}
            </div>
            
            {selectedGenres.length > 0 && (
              <div className="flex justify-center mt-6">
                <button
                  onClick={clearGenres}
                  className="px-6 py-2 bg-red-500/20 hover:bg-red-500/30 border border-red-400/50 rounded-xl text-red-300 transition-all duration-300 transform hover:scale-105"
                >
                  <span className="mr-2">🗑️</span>Clear All ({selectedGenres.length})
                </button>
              </div>
            )}
          </div>
        </div>

        {/* Movies Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
          {movies.map((movie) => (
            <div
              key={movie.imdbID}
              className="group bg-white/5 backdrop-blur-lg rounded-2xl p-4 border border-white/10 hover:bg-white/10 transition-all duration-500 transform hover:scale-105 hover:shadow-2xl hover:shadow-purple-500/20"
            >
              <div 
                onClick={() => setModalMovie(movie)}
                className="cursor-pointer"
              >
                <div className="relative overflow-hidden rounded-xl mb-4">
                  <img
                    src={movie.Poster !== "N/A" ? movie.Poster : "/placeholder.png"}
                    alt={movie.Title}
                    className="w-full h-72 object-cover transition-transform duration-500 group-hover:scale-110"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                  <div className="absolute top-2 right-2 bg-black/60 backdrop-blur-sm rounded-lg px-2 py-1">
                    <span className="text-yellow-400 text-sm">⭐ {movie.imdbRating || "N/A"}</span>
                  </div>
                </div>
                
                <h3 className="text-lg font-semibold mb-2 line-clamp-2 group-hover:text-purple-200 transition-colors">
                  {movie.Title}
                </h3>
                <p className="text-white/60 text-sm mb-1">{movie.Year}</p>
                <p className="text-white/50 text-xs mb-3 line-clamp-1">{movie.Genre}</p>
              </div>
              
              <button
                onClick={() => addToList(movie)}
                className="w-full py-2 bg-blue-500/20 hover:bg-blue-500/30 border border-blue-400/50 rounded-xl text-blue-300 transition-all duration-300 transform hover:scale-105 flex items-center justify-center gap-2 group-hover:shadow-lg"
              >
                <span className="text-lg">+</span>
                <span className="font-medium">Add to List</span>
              </button>
            </div>
          ))}
        </div>

        {/* Empty State */}
        {movies.length === 0 && !loading && selectedGenres.length > 0 && (
          <div className="text-center py-20">
            <div className="text-6xl mb-4">🎬</div>
            <h3 className="text-2xl font-semibold mb-2">No Movies Found</h3>
            <p className="text-white/60 max-w-md mx-auto">
              We couldn't find any movies matching your selected genres. Try different combinations or check back later.
            </p>
          </div>
        )}

        {selectedGenres.length === 0 && (
          <div className="text-center py-20">
            <div className="text-8xl mb-6">🍿</div>
            <h3 className="text-3xl font-semibold mb-4">Ready for Movie Magic?</h3>
            <p className="text-white/60 text-lg max-w-lg mx-auto">
              Select your favorite genres above to discover amazing movies tailored to your taste.
            </p>
          </div>
        )}
      </div>

      {/* Movie Modal */}
      {modalMovie && (
        <div
          className="fixed inset-0 z-50 bg-black/90 backdrop-blur-xl flex items-center justify-center px-4"
          onClick={() => setModalMovie(null)}
        >
          <div
            onClick={(e) => e.stopPropagation()}
            className="bg-white/10 backdrop-blur-lg border border-white/20 rounded-2xl p-6 max-w-2xl w-full shadow-2xl"
          >
            <div className="flex flex-col md:flex-row gap-6">
              <img
                src={modalMovie.Poster !== "N/A" ? modalMovie.Poster : "/placeholder.png"}
                alt={modalMovie.Title}
                className="w-full md:w-64 h-auto rounded-xl"
              />
              <div className="flex-1">
                <h2 className="text-3xl font-bold mb-3 text-white">{modalMovie.Title}</h2>
                <div className="space-y-2 mb-4">
                  <p className="text-white/70"><span className="text-purple-300">Year:</span> {modalMovie.Year}</p>
                  <p className="text-white/70"><span className="text-purple-300">Genre:</span> {modalMovie.Genre || "N/A"}</p>
                  <p className="text-white/70"><span className="text-purple-300">Rating:</span> ⭐ {modalMovie.imdbRating || "N/A"}</p>
                  <p className="text-white/70"><span className="text-purple-300">Cast:</span> {modalMovie.Actors || "Unknown"}</p>
                </div>
                <p className="text-white/80 text-sm italic leading-relaxed">
                  "{modalMovie.Plot || "No description available."}"
                </p>
              </div>
            </div>
            <button
              onClick={() => setModalMovie(null)}
              className="mt-6 w-full py-3 bg-white/10 hover:bg-white/20 border border-white/20 rounded-xl transition-all duration-300"
            >
              Close
            </button>
          </div>
        </div>
      )}

      <style jsx>{`
        .animation-delay-1000 { animation-delay: 1s; }
        .animation-delay-2000 { animation-delay: 2s; }
        .line-clamp-1 {
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        .line-clamp-2 {
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      `}</style>
    </div>
  );
}